<template>
  <div class='activityrule' v-if="showrule" @click="showrule=false">
    <div class="rulebody">
      <div class="ruletitle">
        
      </div>
      <div class="rulecontent">
        <p>{{companyActivity.companyName}}()
          <span v-if="companyActivity.activityType == 1"></span>
          <span v-if="companyActivity.activityType == 2"></span></p>
        <p v-if="companyActivity.activityType == 1"></p>
        <div v-if="companyActivity.activityType == 2">
          <p></p>
          <div v-for="item in discountLevel">
            <p>{{item.discount}}-{{item.mans}}</p>
          </div>
          <p>{{companyActivity.groupRate}}</p>
          <p>{{startTime}}  {{endTime}}</p>
          <p>{{payStartDate}}  {{payEndDate}}</p>
          <p>: {{returnStartDate}}  {{returnEndDate}}</p>
          <p>{{companyActivity.pointsReturnMultiple}}</p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
  
      </div>
      <div class="ruletitle">
        
      </div>
      <div class="rulecontent">
        <ol>
          <li>
            {{company.registerMoney}}{{company.registerPoints}}
          </li>
          <li>{{company.invitedPoints}}</li>
          <li>{{company.sharePoints}}/({{company.shareMax}})</li>
          <li>{{company.oneReturn}}%100{{Math.floor(company.oneReturn)}}{{company.secondReturn}}%100{{company.secondReturn}}</li>
          <li>{{company.selfReturn}}%100{{Math.floor(company.selfReturn)}}</li>
          <li>{{company.toCashRate}}%100{{Math.floor(company.toCashRate)}} 
          </li>
          <li></li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script  type="text/ecmascript-6">
export default {
  name: 'ActivityRule',
  props: ['activity'],
  watch: {
    activity(val) {
      this.showrule = val.showrule
      this.companyActivity = val
      this.getInfoByCompanyId(val.companyId)
      this.startTime = this.util.getFormatDate(val.startDate)
      this.endTime = this.util.getFormatDate(val.endDate)
      this.payStartDate = this.util.getFormatDate(val.payStartDate)
      this.payEndDate = this.util.getFormatDate(val.payEndDate)
      this.returnStartDate = this.util.getFormatDate(val.returnStartDate)
      this.returnEndDate = this.util.getFormatDate(val.returnEndDate)
      this.discountLevel = JSON.parse(val.discountLevel)
    }
  },
  methods: {
    getInfoByCompanyId(val) {
      if (val == void 0 || val == '') return
      this.http.get(this.$store.state.prefix + "/shop/?companyId=" + val).then(res => {
        if (res.error == false) {
          this.company = res.result
        }
      })
    }
  },
  created() {
    this.getInfoByCompanyId(this.activity.companyId)
  },
  data() {
    return {
      company: {},
      companyActivity: {},
      showrule: false,
      startTime: '',
      endTime: '',
      payStartDate: '',
      payEndDate: '',
      returnStartDate: '',
      returnEndDate: '',
      discountLevel:[]
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  rrem(val){
    return (val/108px)rem
  }
  .activityrule
    display:block
    position:fixed
    top rrem(0px)
    left rrem(0px)
    width:100%
    height:100%
    background:rgba(0,0,0,0.8)
    overflow-y:auto
    overflow-x:hidden
    color:#fff
    z-index:11111
    line-height rrem(50px)
    padding:20px 10%
    .ruletitle
      text-align:center
      font-size:1.3em
      border-bottom:1px solid #eee
    .rulecontent
      padding:10px 0px
      p
        text-indent:2em

  .marker
    font-weight:bold
    color:red
  ol
    li
      list-style:decimal
</style>



